<template>
  <div :class="style" ref="like">
    <div class="like-vote">
      <p>{{content}}</p>
      <div>‚ô•{{counter}}</div>
    </div>
  </div>
</template>

<script>
import http from "../http";

export default {
  name: "Like",
  props: {
    text: {
      type: String,
      default: ""
    },
    direction: {
      required: true,
      type: String
    }
  },
  watch: {
    result(newVal) {
      this.requestId = requestAnimationFrame(this.up);
      return newVal;
    }
  },
  data() {
    return {
      time: 5, //ÊåÅÁª≠ÁöÑÁßíÊï∞
      counter: 0, //Êìç‰ΩúÁöÑËÆ°Êï∞Âô®
      result: 0, //ËÆ°Êï∞Âô®ÁöÑÁªìÊûú
      startValue: 0, //ËÆ°Êï∞Âô®ÁöÑÂàùÂßãÂÄº
      startTime: 0, //Â∏ßÂºÄÂßãÊó∂Èó¥
      useEasing: true, //ÊòØÂê¶‰ΩøÁî®ÁºìÂÜ≤Âä®Áîª
      frame: 0, //ÂΩìÂâçÂ∏ßÁöÑÂÄº
      requestId: null, //ÂΩìÂâçÂ∏ßÊï∞
      content: this.text, //textÂ±ïÁ§∫ÂâØÊú¨
      lastClickTime: 0 //Âä†ÂøÉÊúÄÂêéÁÇπÂáªÊó∂Èó¥
    };
  },
  mounted() {
    setTimeout(() => {
      // console.log(window.like = this.$refs.like)
      this.$refs.like.addEventListener("click", this.addLike, false);
    }, this.duration);
    this.getLike();
    this.requestId = requestAnimationFrame(this.up);
  },
  methods: {
    //Â∏ßÊìç‰Ωú
    up(timestamp) {
      if (!this.startTime) {
        this.startTime = timestamp;
      }
      let progress = timestamp - this.startTime;

      if (this.startValue > this.result) {
        //‰ªéÂ∞èÂà∞Â§ß
        if (this.useEasing) {
          this.frame =
            this.startValue -
            this.easing(progress, 0, this.startValue - this.result);
        } else {
          this.frame =
            this.startValue -
            this.general(progress, this.startValue - this.result);
        }

        //‰øùËØÅÂΩìÂâçÂ∏ßÁöÑÂÄº‰∏çË∂ÖËøáÁªìÊûú
        this.frame = this.frame < this.result ? this.result : this.frame;
      } else {
        //‰ªéÂ§ßÂà∞Â∞è
        if (this.useEasing) {
          this.frame = this.easing(progress, 0, this.result - this.startValue);
        } else {
          this.frame =
            this.startValue +
            this.general(progress, this.result - this.startValue);
        }
        this.frame = this.frame > this.result ? this.result : this.frame;
      }

      this.counter = Math.round(this.frame);

      if (progress < this.duration) {
        this.requestId = requestAnimationFrame(this.up);
      }
    },
    //ÁºìÂÜ≤Âä®Áîª
    easing(t, b, c) {
      return (
        (c * (-Math.pow(2, (-10 * t) / this.duration) + 1) * 1024) / 1023 + b
      );
    },
    //‰∏ÄËà¨Âä®Áîª
    general(t, c) {
      return c * (t / this.duration);
    },
    async getLike() {
      let result = await http.getLike(
        "flxxyz",
        "81dc9bdb52d04dc20036dbd8313ed055"
      );

      try {
        result = JSON.parse(result);
        if (result.hasOwnProperty("count")) {
          this.result = result.count;
        }
      } catch (err) {
        console.error(err);
      }
    },
    addLike() {
      //ÊéßÂà∂ÁÇπÂáªÈó¥ÈöîÁöÑÊñáÂ≠ó
      let currClickTime = new Date().getTime();
      let flag = currClickTime - this.lastClickTime > 300;
      this.lastClickTime = new Date().getTime();
      if (flag) {
        this.content = "Áà±‰Ω†Âìü(¬¥‚ñΩ` É‚ô°∆™)";
      } else {
        this.content = "‚àë( Âè£ ||Ë¶ÅÂùèÊéâ‰∫Ü";
      }

      http.addLike("flxxyz", "81dc9bdb52d04dc20036dbd8313ed055");
      this.counter += 1;
    }
  },
  computed: {
    duration() {
      return Number(this.time) * 1000;
    },
    style() {
      return "like";
    }
  }
};
</script>

